@import "./menu";
@import "./headerHero";
@import "./footer";

*,
*::before,
*::after {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    font-size: 16px;
}
.eventItems{
    // width: 90%;
    margin: 125px auto;
    display: grid;
    grid-template-columns: auto 30% 45% auto;
    // grid-template-rows: auto;
    grid-gap: 2%;
    grid-template-areas: 
    ". EventImg EventContent .";
    // @media screen and (max-width: 875px){ 
    //     grid-template-columns: 20px 1FR 1FR 20px;
    //     // grid-template-rows: 1FR;
    //     grid-template-areas: 
    //     ".EventImg EventContent.";
    //     }
    @media screen and (max-width: 875px){ 
        grid-template-columns: auto 90% auto;
        // grid-template-rows: auto 90% auto;
        grid-template-areas: 
        ".EventImg."
        ".EventContent.";
        
        }
   &__img{
    grid-area: EventImg;
    align-self: center;
   }
   &__content{
    grid-area: EventContent;
    // align-self: center;

   }

    &__img{
        grid-area: EventImg;
        border-radius: 35px;
        width:100%;
        // justify-items: center;
        // width:300px;
        // @media screen and (max-width: 906px){ 
        //     width:250px;
        // }
    }
    &__content{
        grid-area: EventContent;
        border: 1px solid grey;
        padding: 20px;
        border-radius: 35px;
        // width: 80%;
        // width:500px;
        // @media screen and (max-width: 906px){ 
        //     width:400px;
        // }
        & hr{
            border: 1px solid #678372;
        }
        &--title{
            font-size:2rem ;
            margin-top: 0;
            color:#000;
        }
        &--info{
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: space-between; 
        }
        &--infoLocation{
            display: flex;
        //     flex-direction: row;
        // flex-wrap: nowrap;
        // justify-content: space-between;
        & img{
            // width: 20%;
            height: 40%;
            margin: 0 8px;
            margin-top:10px;
        }
         & p{
            width: 130px;
            // height: 40%;
            // margin: 0 8px;
            // margin-top:3px;

        }
        }
        &--infoCalender{
            display: flex;
            // flex-direction: row;
            // flex-wrap: nowrap;
            // justify-content: space-between;
            & img{
                // width: 20%;
                height: 40%;
                margin: 0 8px;
                margin-top:10px;

            }
             & p{
                width: 110px;
                // height: 40%;
                // margin: 0 8px;
                // margin-top:3px;

            }
        }
    }
}